:where(.css-dev-only-do-not-override-yp8pcc).ant-card .ant-card-body {
  padding: 0px;
  border-radius: 0 0 8px 8px;
}
.img {
  z-index: 1;
  position: relative;
  width: 100%;
}
.header {
  width: 100%;
  background: linear-gradient(
    to right,
    rgba(31.5, 31.5, 31.5, 1) calc((50vw - 170px) - 340px),
    rgba(31.5, 31.5, 31.5, 0.84) 50%,
    rgba(31.5, 31.5, 31.5, 0.84) 100%
  ) !important;
}
:where(.css-dev-only-do-not-override-yp8pcc).ant-modal .ant-modal-content {
  background-color: black !important;
  padding: 0px !important;
}
:where(.css-dev-only-do-not-override-yp8pcc).ant-modal .ant-modal-title {
  color: white;
  background-color: black;
  margin-left: 0px;
}
:where(.css-dev-only-do-not-override-yp8pcc).ant-modal .ant-modal-close-x {
  color: white;
  margin-top: -25px;
}

.scrolling-wrapper {
  padding-top: 30px;
  width: 100%;
  display: inline-flex;
  height: 32vh;
  overflow: auto;
  overflow-y: hidden;
  z-index: 1;
  width: 100%;
}
.vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
.keyWordsBtn:hover {
  border-color: white !important;
  color: black !important;
}
div.scroll-container {
  background-color: #333;
  overflow: auto;
  white-space: nowrap;
  padding: 10px;
}

.billed-cast-body {
  display: flex;
  .ant-card-cover {
    align-self: center;
  }
  .ant-card-body {
    display: none;
  }
}
